import React, { useCallback } from 'react';
import SimplePostCard from 'components/Cards/SimplePostCard';
import { Post } from 'models/post';
import { linkEvent } from 'utils/loggers';
import { makeStyles, Theme } from '@material-ui/core';

const useStyles = makeStyles<Theme>(({ spacing }) => ({
  root: {
    margin: 0,
    padding: 0,
  },
  item: {
    listStyle: 'none',
    margin: `0 0 ${spacing(2)}px 0`,
    padding: 0,
  },
}));

interface Props {
  posts?: Post[];
  onClickPost?: (url: string) => void;
}
const PostList = ({ posts = [], onClickPost }: Props) => {
  const styles = useStyles();
  return (
    <ul className={styles.root}>
      {posts.map(post => {
        const { frontmatter, fields, excerpt } = post;
        const title = frontmatter.title ?? fields.slug;
        const path = fields.path ?? fields.slug;
        const content = frontmatter.description ?? excerpt ?? '';
        const thumbnail = frontmatter.thumbnail?.childImageSharp.fluid?.src ?? '';
        const date = frontmatter.date;
        const categories = frontmatter.categories;

        return (
          <li key={fields?.slug} className={styles.item}>
            <SimplePostCard
              key={fields?.slug}
              thumbnail={thumbnail}
              title={title}
              path={path}
              content={content}
              date={date}
              categories={categories}
              onClick={onClickPost}
            />
          </li>
        );
      })}
    </ul>
  );
};

export default PostList;
