import React from 'react';
import WidgetCard from 'components/Cards/WidgetCard';
import { useStaticQuery, graphql, Link } from 'gatsby';
import { Group } from 'models/post';
import { Chip, Avatar, makeStyles, Theme } from '@material-ui/core';
import { linkEvent } from 'utils/loggers';
import { useCallback } from 'react';
import kebabCase from 'lodash/kebabCase';

const useStyles = makeStyles<Theme>(({ spacing, typography }) => ({
  nonDecorationText: {
    textDecoration: 'none',
  },
  chip: {
    fontSize: typography.caption.fontSize,
    marginRight: spacing(1),
    marginBottom: spacing(1),
  },
}));

const TagsCard = () => {
  const styles = useStyles();
  const {
    allMarkdownRemark: { group: tags },
  }: Group = useStaticQuery(graphql`
    query TagsQuery {
      allMarkdownRemark {
        group(field: frontmatter___tags) {
          fieldValue
          totalCount
        }
      }
    }
  `);

  const handleTagClick = useCallback((url: string) => {
    linkEvent(url, '태그 링크 클릭');
  }, []);

  return (
    <WidgetCard title="태그">
      {tags.map(tag => (
        <Link key={tag.fieldValue} to={`/tags/${kebabCase(tag.fieldValue)}`} className={styles.nonDecorationText}>
          <Chip
            className={styles.chip}
            size="small"
            label={tag.fieldValue}
            avatar={<Avatar>{tag.totalCount}</Avatar>}
            onClick={() => handleTagClick(`/tags/${kebabCase(tag.fieldValue)}`)}
          />
        </Link>
      ))}
    </WidgetCard>
  );
};

export default TagsCard;
