import React from 'react';
import { Box, makeStyles, Theme } from '@material-ui/core';
import Layout from 'components/Layout/Base';
import BioCard from 'components/Cards/BioCard';
import PostList from 'components/Lists/PostList';
import RecentPostsCard from 'components/Cards/RecentPostsCard';
import CategoriesCard from 'components/Cards/CategoriesCard';
import TagsCard from 'components/Cards/TagsCard';
import Paginator from 'components/Utils/Paginator';
import { Post } from 'models/post';
import { graphql } from 'gatsby';
import { linkEvent } from 'src/utils/loggers';

const useStyles = makeStyles<Theme>(({ spacing }) => ({
  bottomSpace: {
    marginBottom: spacing(2),
  },
}));
const handlePostClick = (url: string) => linkEvent(url, '메인 아티클 링크 클릭');
interface Props {
  posts?: Post[];
  location: Location;
  pageInfo: {
    currentPage: number;
    pageCount: number;
    defaultUrl: string;
    lang: string;
  };
}
const PostsLayout = ({ posts = [], pageInfo }: Props) => {
  const styles = useStyles();

  return (
    <Layout
      left={
        <>
          <Box className={styles.bottomSpace}>
            <BioCard />
          </Box>
          <Box>
            <CategoriesCard />
          </Box>
        </>
      }
      right={
        <>
          <Box className={styles.bottomSpace}>
            <RecentPostsCard />
          </Box>
          <Box>
            <TagsCard />
          </Box>
        </>
      }
    >
      <PostList posts={posts} onClickPost={handlePostClick} />
      <Paginator
        pageCount={pageInfo.pageCount}
        currentPage={pageInfo.currentPage}
        defaultUrl={pageInfo.defaultUrl}
        lang={pageInfo.lang}
      />
    </Layout>
  );
};

export default PostsLayout;

export const query = graphql`
  fragment PostListQuery on MarkdownRemark {
    excerpt(pruneLength: 300)
    fields {
      slug
      path
    }
    frontmatter {
      date(formatString: "YYYY-MM-DD")
      title
      categories
      thumbnail {
        childImageSharp {
          fluid(maxWidth: 800) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  }
`;
