import React, { useMemo } from 'react';
import { Link } from 'gatsby';
import { CardActionArea, CardMedia, CardContent, Typography, Card, makeStyles } from '@material-ui/core';

const useStyles = makeStyles({
  link: {
    color: 'inherit',
    textDecoration: 'none',
  },
});

interface Props {
  thumbnail: string;
  title: string;
  content: string;
  path: string;
  date?: string;
  categories?: string[];
  onClick?: (path: string) => void;
}
const SimplePostCard = ({ thumbnail, title, content, path, date = '', categories = [], onClick }: Props) => {
  const styles = useStyles();
  const category = useMemo(() => {
    if (categories != null) {
      return categories.join('/');
    } else {
      return '';
    }
  }, [categories]);
  return (
    <Card onClick={() => onClick?.(path)}>
      <CardActionArea>
        <Link to={path} className={styles.link}>
          <CardMedia component="img" image={thumbnail} title={title} alt={title} height="280" />
          <CardContent>
            <Typography gutterBottom color="textSecondary" variant="caption" component="p">
              {date} {category}
            </Typography>
            <Typography gutterBottom variant="h6" component="h2">
              {title}
            </Typography>
            <Typography
              color="textSecondary"
              component="p"
              variant="body2"
              dangerouslySetInnerHTML={{ __html: content }}
            />
          </CardContent>
        </Link>
      </CardActionArea>
    </Card>
  );
};

export default SimplePostCard;
